.project-container {
  display: flex;
  flex-direction: column;
  background-color: white;
  width: 600px;
  height: fit-content;
  padding: 20px 30px 10px 30px;
  border-radius: 10px;
  margin-top: 20px;

  @media screen and (max-width: 700px) {
    width: 400px;
    
    
  }

  @media screen and (max-width: 500px) {
    width: 300px;
  }

  @media screen and (max-width: 500px) {
    width: 230px;
  }

  .project-description {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: flex-start;
    justify-content: center;
    text-align: start;
    line-height: 1.8em;
  }

  .project_container {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .project {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 16px;
      border: 1px solid black;
      border-radius: 5px;
      margin-bottom: 40px;

      .title-project-box {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        width: 100%;

        @media screen and (max-width: 500px) {
         flex-direction: column;
        }
      
        
        h3 {
          margin: 0;
        }
        p {
          margin: 0;
          line-height: 1.8em;
          color: $ModerateCyan;
          font-weight: 700;
        }
      }

      .description {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: flex-start;
      }

      .reward-container {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;

        @media screen and (max-width: 500px) {
          flex-direction: column;
          
          align-items: flex-start;
         }

        span {
          display: flex;
          align-items: center;
          @media screen and (max-width: 500px) {
            margin-left: 6px;
            margin-bottom: 10px;
            
           }

          h1 {
            margin: 0;
            margin-right: 6px;
          }

          p {
            margin: 0;
          }
        }

        button {
          padding: 16px 20px;
          border-radius: 20px;
          border: none;
          color: white;
          font-weight: 700;
          background-color: $ModerateCyan;
          cursor: pointer;
          font-family: $Commissioner;

          &:hover {
            background-color: $DarkCyan;
          }
        }
      }
    }

    .project-finished {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 16px;
      border: 1px solid rgba(0, 0, 0, 0.249);
      border-radius: 5px;
      margin-bottom: 20px;

      .title-project-box-finished {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        width: 100%;

        @media screen and (max-width: 500px) {
          flex-direction: column;
        }
      
       
        h3 {
          margin: 0;
          color: rgba(0, 0, 0, 0.249);
        }
        p {
          margin: 0;
          line-height: 1.8em;
          color: rgba(56, 204, 143, 0.249);
          font-weight: 700;
        }
      }

      .description-finished {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: flex-start;

        p {
          color: rgba(0, 0, 0, 0.249);
        }
      }

      .reward-container-finished {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;

        @media screen and (max-width: 500px) {
         flex-direction: column;
        align-items: flex-start;
        }
      
       

        span {
          display: flex;
          align-items: center;

          @media screen and (max-width: 500px) {
            margin-left: 6px;
            margin-bottom: 10px;
            
           }

          h1 {
            margin: 0;
            color: rgba(0, 0, 0, 0.249);
          }

          p {
            margin: 0;
            color: rgba(0, 0, 0, 0.249);
          }
        }

        button {
          padding: 16px 20px;
          border-radius: 20px;
          border: none;
          color: white;
          font-weight: 700;
          background-color: rgba(128, 128, 128, 0.249);
          cursor: pointer;
          font-family: $Commissioner;

          &:hover {
            background-color: $DarkCyan;
          }
        }
      }
    }
  }
}
