.confirm-modal-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.364);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  font-family: $Commissioner;
  .confirm-modal {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 16%;
    width: 400px;
    height: fit-content;
    padding: 30px 30px 30px 30px;
    border-radius: 10px;
    background-color: white;
    margin-top: 20px;

    @media screen and (max-width: 700px) {
      top: 10%;
    }

    @media screen and (max-width: 550px) {
      width: 300px;
      top: 6%;
    }

    @media screen and (max-width: 400px) {
      width: 200px;
    }

    img {
      width: 80px;
      margin: 10px 0;

      @media screen and (max-width: 400px) {
        width: 60px;
      }
    }
    h2 {
      margin: 10px 0;
      @media screen and (max-width: 400px) {
        font-size: 20px;
        text-align: center;
      }
    }
    p {
      color: $DarkGray;
      width: 80%;
      text-align: center;
      line-height: 2em;
      margin: 0;
      @media screen and (max-width: 400px) {
        font-size: 14px;
      }
    }

    button {
      padding: 10px 30px;
      border-radius: 20px;
      border: none;
      color: white;
      font-weight: 700;
      background-color: rgba(128, 128, 128, 0.249);
      cursor: pointer;
      margin-top: 20px;
      font-family: $Commissioner;
      background-color: $ModerateCyan;
      &:hover {
        background-color: $DarkCyan;
      }
    }
  }
}
